import React from 'react';
import { config, useSpring } from 'react-spring';
import Layout from '../components/layout';
import { AnimatedBox } from '../elements';
import SEO from '../components/SEO';

import { Link } from 'gatsby';

const Thanks = () => {
  const pageAnimation = useSpring({
    config: config.slow,
    from: { opacity: 0 },
    to: { opacity: 1 },
  });

  return (
    <Layout color="#000">
      <SEO
        title="About | Bhavisha Patel"
        desc="Hi. I'm Bhavisha Patel! I'm a graphic designer."
      />
      <AnimatedBox
        style={pageAnimation}
        py={[6, 6, 6, 8]}
        px={[6, 6, 8, 6, 8, 13]}
      >
        <h1 style={{ textAlign: 'center', paddingTop: '20px' }}>Thank you!</h1>

        <div style={{ marginTop: '32px' }}>
          <p style={{ textAlign: 'center', paddingBottom: '20px' }}>
            You can also reach out to me using one of the following methods.
          </p>
          <section>
            <div className="contact-grid-container">
              <div className="contact-grid-item">
                <a
                  href="mailto:bhavishampatel@gmail.com"
                  title="Email Bhavisha"
                  aria-label="email Bhavisha"
                >
                  <img
                    src="/images/email.svg"
                    alt="Email"
                    className="contact-icon"
                  />
                </a>
              </div>
              <div className="contact-grid-item">
                <a
                  href="tel:647-533-7473"
                  title="Call Bhavisha"
                  aria-label="call Bhavisha"
                >
                  <img
                    src="/images/phone.svg"
                    alt="Phone"
                    className="contact-icon"
                  />
                </a>
              </div>
              <div className="contact-grid-item">
                <a
                  href="https://ca.linkedin.com/in/bhavishampatel"
                  target="_blank"
                  title="Tweet Bhavisha"
                  aria-label="tweet Bhavisha"
                >
                  <img
                    src="/images/linkedin.svg"
                    alt="Linkedin Profile Bhavisha"
                    className="contact-icon"
                  />
                </a>
              </div>
            </div>
          </section>
        </div>
      </AnimatedBox>
    </Layout>
  );
};

export default Thanks;
